.jenkins-config-widgets {
  position: relative;
  border: 1px solid var(--input-border);
  border-bottom: none;
  background: var(--light-grey);
  min-height: 2.5em;
  -webkit-border-top-left-radius: 3px;
  -moz-border-top-left-radius: 3px;
  border-top-left-radius: 3px;
  -webkit-border-top-right-radius: 3px;
  -moz-border-top-right-radius: 3px;
  border-top-right-radius: 3px;
  z-index: 5;
}
.jenkins-config-widgets nav:before,
.jenkins-config-widgets nav:after {
  display: none;
}
.jenkins-config-widgets :hover .noTabs {
  color: #bbb;
}
.jenkins-config-widgets .noTabs:hover,
.jenkins-config-widgets .showTabs:hover {
  opacity: 0.8;
  background: #bbb;
  color: var(--light-bg-color);
  text-shadow: none;
}
.jenkins-config-widgets .find-container {
  margin: 0;
  display: none;
}
.jenkins-config-widgets .showTabs,
.jenkins-config-widgets .noTabs {
  position: absolute;
  border-radius: var(3px);
  right: 5px;
  top: 5px;
  cursor: pointer;
  color: var(--medium-grey);
  text-shadow: var(--brightest-bg-color 0 1px 2px);
  font-size: var(--font-size-sm);
  padding: 3px 7px;
  display: none;
}
.jenkins-config-widgets .form-config.tabBarFrame {
  position: relative;
  border-bottom: solid 1px var(--medium-grey);
}
.jenkins-config-widgets .form-config.tabBarFrame .config-section-activators {
  margin: 0;
  padding: 5px 0 0;
  display: block;
  list-style: none;
}
.jenkins-config-widgets .form-config.tabBarFrame .tabBar .tab {
  border: solid 1px transparent;
  color: var(--text-color-secondary);
  padding: 7px 10px;
  -webkit-border-top-left-radius: 5px;
  -moz-border-top-left-radius: 5px;
  border-top-left-radius: 5px;
  -webkit-border-top-right-radius: 5px;
  -moz-border-top-right-radius: 5px;
  border-top-right-radius: 5px;
  cursor: pointer;
  margin: 1px 0 0 10px;
  text-decoration: none;
}
.jenkins-config-widgets .form-config.tabBarFrame .tabBar .tab:hover {
  background: var(--very-light-grey);
  color: var(--text-color-secondary);
  border: 1px solid var(--medium-grey);
  border-bottom: 1px solid var(--bright-bg-color);
}
.jenkins-config-widgets .form-config.tabBarFrame .tabBar .tab.active {
  background: var(--background);
  color: var(--text-color);
  font-weight: bold;
  z-index: 2;
  border: 1px solid var(--medium-grey);
  border-bottom: 1px solid var(--bright-bg-color);
}
.jenkins-config-widgets .form-config.tabBarFrame.mouse-over .noTabs {
  display: inline;
}
.jenkins-config-widgets .form-config.tabBarFrame.mouse-over .noTabs:hover {
  opacity: 0.8;
}
#jenkins .jenkins-config {
  border: 1px solid var(--input-border);
  padding: 10px;
  border-top: none;
  background: var(--bright-bg-color);
  -webkit-border-bottom-left-radius: 3px;
  -moz-border-bottom-left-radius: 3px;
  -webkit-border-bottom-right-radius: 3px;
  -moz-border-bottom-right-radius: 3px;
  border-top-bottom-radius: 3px;
}
#jenkins .jenkins-config .showTabs {
  display: block;
  text-align: right;
}
#jenkins .jenkins-config table {
  border-collapse: collapse;
}
#jenkins .jenkins-config table td {
  padding: 5px;
}
#jenkins .jenkins-config table .repeated-chunk,
#jenkins .jenkins-config table tr[nameref^="rowSetStart"],
#jenkins .jenkins-config table tr[nameref^="cb"],
#jenkins .jenkins-config table tr[nameref^="radio-block"] {
  background: var(--shade-hint);
  border-left: 5px solid var(--shade);
}
#jenkins .jenkins-config table .hetero-list-container,
#jenkins .jenkins-config table .repeated-container,
#jenkins .jenkins-config table tr.optional-block-start,
#jenkins .jenkins-config table tr.radio-block-start {
  border-left: 5px solid var(--shade);
  background: none;
}
#jenkins .jenkins-config .config-table table,
#jenkins .jenkins-config .config-table .table {
  border-collapse: collapse;
}
#jenkins .jenkins-config .config-table input[type='email'].setting-input,
#jenkins .jenkins-config .config-table input[type='password'].setting-input,
#jenkins .jenkins-config .config-table input[type='text'].setting-input {
  height: var(--input-line-height);
  line-height: var(--input-line-height);
  padding: 0 5px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
#jenkins .jenkins-config .config-table select:not([multiple]) {
  height: var(--input-line-height);
  line-height: var(--input-line-height);
}
#jenkins .jenkins-config .config-table select:not(.select-ajax-pending) {
  border-radius: 0;
  background: var(--brightest-bg-color);
}
#jenkins .jenkins-config .config-table .bottom-sticker-edge {
  display: none;
}
#jenkins .jenkins-config .config-table .bottom-sticker-inner {
  padding: 20px 10px 25px 20px;
  border-top-right-radius: 10px;
  border: 1px solid var(--configure-job-bottom-sticker-border-color);
  background: var(--configure-job-bottom-sticker-bg-color);
}
#jenkins .jenkins-config .config-table .bottom-sticker,
#jenkins .jenkins-config .config-table #bottom-sticker {
  width: auto;
  margin-left: -10px;
  overflow: hidden;
  padding-bottom: 10px;
  margin: 30px 30px -25px -15px;
  z-index: 9;
}
#jenkins .jenkins-config .config-table .bottom-sticker .cover,
#jenkins .jenkins-config .config-table #bottom-sticker .cover {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
}
#jenkins .jenkins-config .config-table p,
#jenkins .jenkins-config .config-table .help,
#jenkins .jenkins-config .config-table .setting-description {
  font-size: var(--font-size-sm);
}
#jenkins .jenkins-config .config-table tr.help-area,
#jenkins .jenkins-config .config-table .tr.help-area {
  background: none !important;
}
#jenkins .jenkins-config .config-table tr.help-area td,
#jenkins .jenkins-config .config-table .tr.help-area td,
#jenkins .jenkins-config .config-table tr.help-area .td,
#jenkins .jenkins-config .config-table .tr.help-area .td {
  padding: 0 5px;
}
#jenkins .jenkins-config .config-table .repeated-container > .repeated-chunk {
  padding: 0.75rem;
}
#jenkins .jenkins-config .config-table .repeated-chunk.hover {
  border-color: var(--line-blue);
  box-shadow: 0 2px 10px var(--shade), inset 0 200px 200px -200px var(--brightest-bg-color);
  position: relative;
  z-index: 2;
}
#jenkins .jenkins-config .config-table .repeated-chunk {
  border: 1px solid var(--shade);
  margin: 2px;
  position: relative;
}
#jenkins .jenkins-config .config-table .repeated-chunk.repeated-chunk--sortable-ghost,
#jenkins .jenkins-config .config-table .repeated-chunk.repeated-chunk--sortable-chosen {
  border-width: 2px;
  border-color: var(--line-blue);
}
#jenkins .jenkins-config .config-table .repeatable-delete {
  position: absolute;
  top: -4px;
  right: 40px;
  height: 20px;
  width: 30px;
}
#jenkins .jenkins-config .config-table .repeatable-delete button {
  text-align: center;
  text-indent: -9999px;
  width: 30px !important;
  height: 18px !important;
  padding: 0 !important;
  margin: 0 !important;
  -webkit-border-top-left-radius: 0 !important;
  -moz-border-top-left-radius: 0 !important;
  border-top-left-radius: 0 !important;
  -webkit-border-top-right-radius: 0 !important;
  -moz-border-top-right-radius: 0 !important;
  border-top-right-radius: 0 !important;
  background-color: var(--danger);
  color: var(--light-bg-color);
  border: 1px solid var(--danger-line);
  line-height: 12px !important;
  display: inline-block;
  font-size: 8px;
  min-width: 0;
  min-height: 0;
}
#jenkins .jenkins-config .config-table .repeatable-delete button:focus,
#jenkins .jenkins-config .config-table .repeatable-delete button:hover {
  background-color: var(--danger-dark);
  border-color: var(--danger-dark-line);
  color: var(--brightest-bg-color);
}
#jenkins .jenkins-config .config-table .repeatable-delete button:before {
  content: 'X';
  font-weight: bold;
  position: absolute;
  display: block;
  left: 0;
  top: 2;
  width: 30px;
  height: 16px;
  text-align: center;
  font-size: 12px;
  text-indent: 0;
}
#jenkins .jenkins-config .config-table span.highlight {
  background-color: #ffff00;
}
#jenkins .jenkins-config .config-table .CodeMirror {
  background: var(--brightest-bg-color);
}
#jenkins .jenkins-config .config-table .CodeMirror-scroll {
  border: 1px solid var(--input-border) !important;
}
.yui-skin-sam .yuimenu {
  z-index: 9999 !important;
}
.yui-skin-sam .yui-button-disabled {
  opacity: 0.75;
